import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { Camera } from '@ionic-native/camera';
import { Crop } from '@ionic-native/crop';
import { File } from '@ionic-native/file';
import { Transfer } from '@ionic-native/transfer';
import { FilePath } from '@ionic-native/file-path';

import { HttpModule } from '@angular/http';
import { IonicStorageModule } from '@ionic/storage';
import { EnvironmentsModule } from './../app/environment-variables/environment-variables.module';
import { Facebook } from '@ionic-native/facebook';

import { MyApp } from './app.component';
import { LoginPage } from '../pages/login/login';

import { AuthProvider } from '../providers/auth/auth';
import { TodosProvider } from '../providers/todos/todos';

import { EmailValidator } from '../validators/email';
import { ImageProvider } from '../providers/image/image';
import { UserProvider } from '../providers/user/user';

@NgModule({
  declarations: [
    MyApp,
    LoginPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
    EnvironmentsModule,
    IonicModule.forRoot(MyApp, {
      tabsHideOnSubPages: true
    }, 
{
  links: [
    { loadChildren: '../pages/login/login.module#LoginPageModule', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/current/current.module#CurrentPageModule', name: 'CurrentPage', segment: 'current', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/dashboard/dashboard.module#DashboardPageModule', name: 'DashboardPage', segment: 'dashboard', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/profile/profile.module#ProfilePageModule', name: 'ProfilePage', segment: 'profile', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/search-trip/search-trip.module#SearchTripPageModule', name: 'SearchTripPage', segment: 'search-trip', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/signup/signup.module#SignupPageModule', name: 'SignupPage', segment: 'signup', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/start-trip/start-trip.module#StartTripPageModule', name: 'StartTripPage', segment: 'start-trip', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tabs/tabs.module#TabsPageModule', name: 'TabsPage', segment: 'tabs', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/travels/travels.module#TravelsPageModule', name: 'TravelsPage', segment: 'travels', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    LoginPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    Storage,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    AuthProvider,
    TodosProvider,
    Facebook,
    EmailValidator,
    Camera,
    File,
    Transfer,
    FilePath,
    ImageProvider,
    UserProvider,
    Crop
  ]
})
export class AppModule {}



